@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap);
*{box-sizing:border-box;margin:0;padding:0;font-family:'Nanum Gothic', sans-serif}html,body,#root{width:100vw;min-height:100vh}#root{color:#ffffff;background-color:#51a9e3}#root a{text-decoration:none}

#bigWrapper{width:100vw;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;min-height:130vh;display:flex;flex-direction:column;justify-content:space-between}#bigWrapper .page{width:100vw;min-height:100vh;position:relative}#bigWrapper .selfIntro .selfIntroContainer{width:100%;display:flex;flex-direction:row;justify-content:space-around;align-items:center;margin-top:8%}#bigWrapper .iconContainer{width:100%;height:100vh;padding:10vh 10vh;display:flex;flex-direction:column;flex-wrap:wrap;justify-content:space-around}#bigWrapper .toyProject{display:flex;flex-direction:column;align-items:center}

button{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;border:none;background-color:transparent;position:fixed;top:5vh;right:5vh}button img{width:4.5em;height:4.5em}

h1{text-align:center;font-size:5vh;color:#ffffff;padding-top:5vh}

footer{width:100vw;height:30vh;background-color:#050586;font-size:2em;display:flex;flex-direction:column;justify-content:center;align-items:center}footer .line{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}footer .line img{width:1em;height:1em;float:left}footer .line p{margin-left:1.5em}footer .line+.line{margin-top:1.5em}

.card{width:80vw;height:40vh;margin:3em 0;padding:1.5em 1.5em;line-height:1.5em;border:1.5em solid #f1c40f;border-radius:1.5em;background-color:#ffffff}.card.makeItFit{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:40vh}.card *{color:darkblue}.card img{width:15vw;height:15vw;float:left;margin-right:1.5em}.card .projectTitle{font-size:2vw;font-weight:900;margin:1em 0}.card p{font-size:1vw;font-weight:700;margin:1em 0}.card span{display:inline-block;min-width:5em;height:1.5em;color:yellow;background-color:#050586;border-radius:.5em;text-align:center;line-height:.1em;padding:1em;margin:1em 0}.card span+span{margin-left:.5em}

div{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}div .languageIcon{min-width:128px;min-height:128px;max-width:60vh;max-height:60vh;height:20vh;margin:.5em .5em}div .bigIcon{width:60vh;height:60vh}

.selfImgContainer{width:60vh;height:60vh;position:relative}.selfImgContainer>div{position:absolute;width:60vh;height:60vh;overflow:hidden;border-radius:50%;border:2em solid gray}.selfImgContainer>div>img{width:100%}.selfImgContainer .hexagon{position:absolute;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.selfImgContainer .hexagon.topleft{top:calc(50% - 50% / 1.414);left:calc(50% - 50% / 1.414)}.selfImgContainer .hexagon.topcenter{top:0;left:50%;transform:translateX(-50%) translateY(15%)}.selfImgContainer .hexagon.topright{top:calc(50% - 50% / 1.414);right:calc(50% - 50% / 1.414)}.selfImgContainer .hexagon.middleleft{top:50%;left:0;transform:translateX(20%)}.selfImgContainer .hexagon.middleright{top:50%;right:0;transform:translateX(-20%)}.selfImgContainer .hexagon.bottomleft{bottom:calc(50% - 50% / 1.414);left:calc(50% - 50% / 1.414)}.selfImgContainer .hexagon.bottomcenter{bottom:0;left:50%;transform:translateX(-50%) translateY(-15%)}.selfImgContainer .hexagon.bottomright{bottom:calc(50% - 50% / 1.414);right:calc(50% - 50% / 1.414)}

