@import './info';

.selfImgContainer {
    width: 100vh * 0.6;
    height: 100vh * 0.6;
    
    position: relative;
    
    > div {
        position: absolute;
        width: 100vh * 0.6;
        height: 100vh * 0.6;
        overflow: hidden;
        border-radius: 50%;
        border: 2em solid gray;
        > img {
            width: 100%;
        }
    }

    & .hexagon {
        $coordinate: calc(50% - 50% / 1.414);
        position: absolute;
        width: fit-content;

        
        &.topleft {
            top: $coordinate;
            left: $coordinate;
        }

        &.topcenter {
            top: 0;
            left: 50%;
            transform: translateX(-50%) translateY(15%);
        }

        &.topright {
            top: $coordinate;
            right: $coordinate;
        }
        &.middleleft {
            top: 50%;
            left: 0;
            transform: translateX(20%);
        }

        &.middleright {
            top: 50%;
            right: 0;
            transform: translateX(-20%);
        }
        &.bottomleft {
            bottom: $coordinate;
            left: $coordinate;
        }

        &.bottomcenter {
            bottom: 0;
            left: 50%;
            transform: translateX(-50%) translateY(-15%);
        }

        &.bottomright {
            bottom: $coordinate;
            right: $coordinate;
        }
    }
}