@import './info';

#bigWrapper {
    width: 100vw;
    height: fit-content;
    min-height: 100vh * 1.3;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    & .page {
        width: 100vw;
        min-height: 100vh;
        position: relative;
    }

    & .selfIntro {
       & .selfIntroContainer {
            width: 100%;
            display: flex;
            flex-direction: row;
            justify-content: space-around;
            align-items: center;
            margin-top: 8%;
       }
    }

    & .iconContainer {
        width: 100%;
        height: 100vh;
        padding: 100vh*0.1 100vh*0.1;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        justify-content: space-around;
    }

    & .toyProject {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}