@import './info';

.card {
    width: 100vw * 0.8;
    height: 100vh * 0.4;
    margin: 3em 0;
    padding: 1.5em 1.5em;
    line-height: 1.5em;
    border: 1.5em solid rgb(241, 196, 15);
    border-radius: 1.5em;
    background-color: #ffffff;
    
    &.makeItFit {
        width: fit-content;
        height: 100vh * 0.4;
    }

    * {
        color: darkblue;
    }

    & img {
        width: 100vw * 0.15;
        height: 100vw * 0.15;
        float: left;
        margin-right: 1.5em;
    }

    & .projectTitle {
        font-size: 100vw * 0.02;
        font-weight: 900;
        margin: 1em 0;
    }

    & p {
        font-size: 100vw * 0.01;
        font-weight: 700;
        margin: 1em 0;
    }

    & span {
        display: inline-block;
        min-width: 5em;
        height: 1.5em;
        color: yellow;
        background-color: $footer_color;
        border-radius: .5em;
        text-align: center;
        line-height: .1em;
        padding: 1em;
        margin: 1em 0;
    }

    span + span {
        margin-left: .5em;
    }
}