@import './info';

footer {
    width: 100vw;
    height: $footer_height;
    background-color: $footer_color;
    font-size: 2em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    & .line {
        width: fit-content;

        & img {
            width: 1em;
            height: 1em;
            float: left;
        }

        & p {
            margin-left: 1.5em;
        }
    }

    .line + .line {
        margin-top: 1.5em;
    }
}